video {
  display: none;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

button {
  padding: 15px;
  border: none;
}
.btn-01 {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.btn-02 {
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 2;
}



